.dx-datagrid {
	background: none;
}
.dx-datagrid .dx-row > td {
	font-family: DINREG;
	font-size: 1rem;
	white-space: pre-wrap;
}
.dx-datagrid-text-content {
	padding: 4px;
}
.dx-datagrid-text-content,
.dx-datagrid-headers .dx-datagrid-table .dx-row > td {
	font-family: DINMED;
	color: #000;
	font-size: 1rem;
}
.dx-command-select {
	padding: 0 !important;
	width: 60px !important;
}
.dx-datagrid .dx-row > td {
	padding-top: 8px !important;
	padding-bottom: 8px !important;
}
.elem-center {
	display: flex;
	width: 100vw;
	height: 100vh;
	overflow: hidden;
	justify-content: center;
	align-items: center;
	background-color: #fff;
	transition: all 4s cubic-bezier(0.55, 0.055, 0.675, 0.19);
}
.dx-row .dx-column-lines .dx-datagrid-filter-row .dx-editor-cell .dx-focused {
	padding: 0 !important;
	text-align: center;
}
.dx-editor-cell .dx-texteditor .dx-texteditor-input {
	text-align: center;
}
.dx-calendar-body thead {
	font-size: 0.9rem;
	font-weight: bold;
}
.dx-calendar-cell {
	font-size: 0.9rem;
}
.dx-datagrid .dx-header-filter-empty {
	color: #333;
}
.dx-toolbar-button.dx-toolbar-text-auto-hide .dx-button .dx-icon {
	font-size: 22px;
	width: 24px;
	height: 24px;
}

.dx-rtl.dx-toolbar .dx-toolbar-item {
	font-size: 1rem;
}

.dx-rtl .dx-datagrid-headers .dx-datagrid-table,
.dx-rtl .dx-datagrid-rowsview .dx-datagrid-table,
.dx-rtl .dx-datagrid-total-footer .dx-datagrid-table {
	/* direction: ltr; */
}
